export default {
	data() {
		return {
			searchlist:[],
			myword:"",
			records:"",
			page:2,
			wancheng:false
		}
	},
	methods: {
		postsearch(keywords,page,pageSize) {
			uni.showNavigationBarLoading()
			uni.showLoading({
				title: '加载中'
			})
			uni.request({
				url:"https://www.imovietrailer.com/superhero/search/list?keywords=" + keywords + "&page=" + page + "&pageSize=" + pageSize + "&qq=3129996800",
				method:"POST",
				success: (res) => {
					if(res.data.status = 200){
						//console.log(res.data)
						this.searchlist =[...this.searchlist,...res.data.data.rows];
						this.records = res.data.data.records;
						
					}
					
				},
				complete: () => {
					uni.hideLoading();
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
				}
			})

		},
		mysearch(e) {
			this.searchlist =[];
			this.myword = e.detail.value;
			this.postsearch(this.myword,1,6);
		},
		todetails(id) {
			uni.navigateTo({
				url: '/pages/details/details?id='+id
			})
		}
	},
	onReachBottom() {
		
		if((this.page - 1) * 6<= this.records){
			//console.log(111);
			this.postsearch(this.myword,this.page,6);
			this.page++				
		}else{
			this.wancheng =true
		}

	},
	onLoad() {
		this.postsearch("",1,6)
	},
	onShareAppMessage() {
		return {
		  title: '搜索预告片',
		  path: 'pages/search/search'
		}
	},
	onPullDownRefresh() {
		//console.log('refresh');
		this.searchlist =[];
		this.postsearch("",1,6);
	}
}
